import i18n from 'i18next';
import { initReactI18next } from 'react-i18next';

i18n
  .use(initReactI18next)
  .init({
    debug: true,
    fallbackLng: 'en',
    interpolation: {
      escapeValue: false,
    },
    resources: {
      en: {
        translation: {
         welcome: "Welcome on my page!",
         language: "English",
         home: "Home",
         projects: "Projects",
         summary: "Summary",
         descWelcome: "My name is Tomasz Wiejak and I have a engineering degree of Computer Science. Programming gives me a lot of enjoyment and is one of the things I do in my spare time. I created this page to present sample projects created by me and to show the technologies that I mainly use. I invite you to watch this site!",
         descTower: "A Tower Defense game that uses procedural generation mechanism. The game was written in Java with the LibGDX framework. Based on random seed, a map, waves of enemies and rewards for defeating them are generated. The game has an advanced system of passive upgrades that pass between successive games on the same profile. There's a possibility to save both locally and in the cloud. The server uses Microsoft Azure hosting.",
         titleStatistics: "Statistics site",
         descStatistics: "A website designed to present statistics of marriages and divorces in individual provinces based on the population density of a given province. The data is downloaded via API from the local data bank from the website of the Central Statistical Office (Polish GOV site). Recharts is responsible for generating charts.",
         descSummary: "Thank you for visiting my website. Below is my CV in Polish and English. In addition, there is nearby a technology stack that I mainly use in the form of a scratch card. You can reveal them by scratching them or clicking the appropriate button.",
         descCV1: "CV in Polish",
         descCV2: "CV in English",
         Reveal: "Reveal"
        }
      },

      pl: {
        translation: {
         welcome: "Witaj na mojej stronie!",
         language: "Polski",
         home: "Pocztek ",
         projects: "Projekty",
         summary: "Podsumowanie",
         descWelcome: "Nazywam si Tomasz Wiejak i mam tytu inyniera informatyki. Programowanie sprawia mi duo przyjemnoci i jest jedn z rzeczy, ktre robi w wolnym czasie. Stworzyem t stron, aby przedstawi przykadowe projekty stworzone przez mnie i pokaza technologie, z ktrych gwnie korzystam. Zapraszam do obejrzenia tej strony!",
         descTower: "Gra typu Tower Defense wykorzystujca mechanizm generowania proceduralnego. Gra zostaa napisana w Javie na szkielecie programistycznym LibGDX. Na podstawie losowego ziarna tworzona jest mapa, fale przeciwnikw, nagrody za ich pokonanie i tym podobne. Gra zawiera zaawansowany system ulepsze pasywnych przechodzcy pomidzy kolejnymi rozgrywkami na tym samym profilu. Istnieje moliwo zapisu zarwno lokalnie, jak i w chmurze. Serwer wykorzystuje hosting na platformie Microsoft Azure.",
         titleStatistics: "Strona ze statystykami",
         descStatistics: "Strona majca za zadanie obrazowa statystki maestw i rozwodw w poszczeglnych wojewdztwach na podstawie gstoci zaludnienia danego wojewdztwa. Dane s pobierane przez API z banku danych lokalnych ze strony Gwnego Urzdu Statystycznego. Za generowanie wykresw odpowiada biblioteka Recharts.",
         descSummary: "Dzikuj Ci, e odwiedzie/a moj stron. Poniej znajduje si moje CV w jzyku polskim i angielskim. Dodatkowo obok znajduje si zbir technologii, ktrymi si gwnie posuguj w formie zdrapki. Mona je odsoni zdrapujc lub klikajc odpowiedni przycisk.",
         descCV1: "CV w jzyku polskim",
         descCV2: "CV w jzyku angielskim",
         Reveal: "Odso"
        }
      },
    }
  });

export default i18n;